import {FC} from "react";
import "./MediaPreview.scss";
import FeedCard from "../feedCard";
import Advertisement from "../advertisement";

import {ReactComponent as Go} from "../../../icons/goOrange.svg";

interface MediaPreviewProps {
  
}

const MediaPreview: FC<MediaPreviewProps> = ({}) => {
  return (
    <section className={'mediaPreview'}>
      <div className={'mediaPreview__info info'}>
        <h2 className={'info__heading'}></h2>
        <div className={'info__content content'}>
          <div className={'content__mediaList mediaList'}>
            <div className={'mediaList__navigation navigation'}>
              <span className={'navigation__item'}></span>
              <span className={'navigation__item'}></span>
              <span className={'navigation__item'}>
                  
                <Go/>
              </span>
            </div>
            <Advertisement/>
            <FeedCard type={'photo'}
                      size={'big'}
                      heading={'        -  '}/>
            <FeedCard type={'photo'}
                      heading={'       '}/>
            <FeedCard type={'photo'}
                      heading={'     '}/>
          </div>
        </div>
      </div>
    </section>
  )
}

export default MediaPreview;