import {FC} from "react";
import "./newsPage.scss";
import FeedCard from "../../../UI/feedCard";
import Advertisement from "../../../UI/advertisement";
import Newsline from "../../../UI/newsline";
import Popular from "../../../UI/popular";
import SpecProjectsPreview from "../../../UI/specProjectsPreview";
import Economics from "../../../UI/economics";
import FeedPreview from "../../../UI/feedPreview";
import MediaPreview from "../../../UI/mediaPreview";

interface SpecProjectsProps {

}

const NewsPage: FC<SpecProjectsProps> = ({}) => {
  return (
    <section className={'specProjects'}>
      <section className={'specProjects_centered specProjects__content content'}>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <FeedCard type={'full'} heading={'-        Nemoloko'}/>
        <Newsline heading={' '} isTopHighlighted={true}/>
        <FeedCard type={'full'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <Popular/>
      </section>
      <section className={'specProjects__preview preview'}>
          <SpecProjectsPreview/>
      </section>
      <section className={'specProjects_centered specProjects__additionalContent additionalContent'}>
        <aside className={'additionalContent__aside aside'}>
          <Newsline heading={''}/>
          <Advertisement/>
          <Advertisement/>
          <Advertisement/>
        </aside>
        <section className={'additionalContent__newsFeed newsFeed'}>
          <Advertisement/>
          <Advertisement/>
          <FeedCard type={'half'}
                    heading={'         '}
                    size={'medium'}/>
          <FeedCard type={'half'}
                    heading={'         '}
                    size={'medium'}/>
          <FeedCard type={'half'}
                    heading={'         '}
                    size={'medium'}/>
          <FeedCard type={'half'}
                    heading={'         '}
                    size={'medium'}/>
          <FeedCard type={'full'}
                    heading={'         '}/>
        </section>
      </section>
      <section className={'specProjects__feedPreview'}>
        <FeedPreview/>
      </section>
      <section className={'specProjects_centered specProjects__economicsContent economicsContent'}>
        <Economics/>
      </section>
      <section className={'specProjects__media'}>
        <MediaPreview/>
      </section>
      <section className={'specProjects_centered specProjects__ads'}>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
      </section>
    </section>
  )
}

export default NewsPage;