import {FC} from "react";
import "./SpecProjectsPreview.scss";
import FeedCard from "../feedCard";

interface SpecProjectsPreviewProps {

}

const SpecProjectsPreview: FC<SpecProjectsPreviewProps> = ({}) => {
  return (
    <section className={'specProjectsPreview'}>
      <div className={'specProjectsPreview__info info'}>
        <h2 className={'info__heading'}></h2>
        <div className={'info__list list'}>
          <div className={'list__item item'}>
            <h3 className={'item__heading item__heading_orange'}> </h3>
            <FeedCard type={'slider'} heading={' ""        10%'}/>
          </div>
          <div className={'list__item item'}>
            <h3 className={'item__heading item__heading_white'}> </h3>
            <FeedCard type={'slider'} heading={'         380 '}/>
          </div>
        </div>
      </div>
    </section>
  )
}

export default SpecProjectsPreview;